import { environment } from "./../../../environments/environment";
import { Component, OnInit, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from "@angular/core";
import { FormsModule, Validators } from "@angular/forms";

import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { LocationService } from "../../shared/location/location.service";
import { RegionService } from "../../shared/region/region.service";
import { RoleService } from "../../shared/role/role.service";
import { TeamService } from "../../shared/team/team.service";
import { AreaService } from "../../shared/area/area.service";
import { User } from "../../shared/user/user";
import { AuthService } from "../../shared/user/auth.service";
import { UserService } from "../../shared/user/user.service";
import { Team } from "../../shared/team/team";
import { BasicFunctions } from "../../shared/utility/basic-functions";
import { Router } from "@angular/router";

@Component({
  selector: "app-team",
  templateUrl: "./team.component.html",
  styleUrls: ["./team.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TeamComponent implements OnInit {
  @ViewChild("createUserModal") createUserModal;
  @ViewChild("userDetailsModal") userDetailsModal;
  @ViewChild("deleteUserModal") deleteUserModal;
  @ViewChild("createFile") createFile;
  @ViewChild("usersModal") usersModal;
  @ViewChild("body") body;

  // team
  @ViewChild("teamDetailsModal") teamDetailsModal;
  @ViewChild("confirmDeactivateTeamModal") confirmDeactivateTeamModal;
  @ViewChild("teamFormModal") teamFormModal;

  public tempUsers: BehaviorSubject<Array<User>>;
  public tempTeamUsers: Array<any>;
  public users: BehaviorSubject<Array<User>>;
  public countries: BehaviorSubject<Array<{ label: string; value: string }>>;
  public roles: BehaviorSubject<Array<{ label: string; value: string }>>;
  public teams: BehaviorSubject<Array<Team>>;
  public teamCount: number;
  public teamOptions: BehaviorSubject<Array<{ label: string; value: string }>>;
  public selectedTeam: Team;
  public user: User;
  public userCount: number;
  public tempCountry;
  public fetchDone: boolean;
  public searchTerm; // suppress compiler error
  public hidden; // suppress compiler error
  public isEditNotCreate: boolean;
  public countryDialCode;
  public TeamActive: boolean;
  public RolesActive: boolean;
  public activeFabs: boolean;
  public order: string;
  public userOrder: string;

  // team
  public teamDetails: Team;
  public teamForDelete: Team;
  public addTeam: Team;
  public mainTeams: BehaviorSubject<Array<Team>>;
  public teamMembers: Array<any>;
  public country: BehaviorSubject<Array<any>>;
  public selectedCountry: string;
  public selectedRegion: string;
  public regions: BehaviorSubject<Array<any>>;
  public aoo: BehaviorSubject<Array<any>>;
  public selectedArea;
  public teamUsers: Array<any>;
  public states: BehaviorSubject<Array<any>>;
  public selectedState: string;
  public searchQuery: string;
  public CountryActive: boolean;
  public RegionActive: boolean;
  public AreaActive: boolean;
  public SupervisorActive: boolean;
  public StateActive: boolean;
  public MembersActive: boolean;
  public teamAction: string;

  constructor(
    public userService: UserService,
    public roleService: RoleService,
    private locationService: LocationService,
    public teamService: TeamService,
    private authService: AuthService,
    public areaService: AreaService,
    public regionService: RegionService,
    private ref: ChangeDetectorRef,
    private router: Router,
    public basicFunctions: BasicFunctions
  ) {
    this.userCount = 0;
    this.user = new User();
    this.searchTerm = "";
    this.tempUsers = new BehaviorSubject([]);
    this.users = new BehaviorSubject([]);
    this.countries = new BehaviorSubject([]);
    this.roles = new BehaviorSubject([]);
    this.teams = new BehaviorSubject([]);
    this.teamOptions = new BehaviorSubject([]);
    this.fetchDone = false;
    this.countryDialCode = "";
    this.selectedTeam = new Team();
    this.teamAction = "";

    this.CountryActive = false;
    this.TeamActive = false;
    this.RolesActive = false;
    this.activeFabs = false;
    this.StateActive = false;
    this.RegionActive = false;
    this.AreaActive = false;
    this.SupervisorActive = false;
    this.MembersActive = false;
    this.order = "teamName";

    // teams
    this.teamDetails = new Team();
    this.addTeam = new Team();
    this.teamForDelete = new Team();
    this.mainTeams = new BehaviorSubject([]);
    this.teamCount = 0;

    this.country = this.locationService.countries;
    this.regions = this.regionService.countryRegions;
    this.states = this.regionService.states;
    this.aoo = this.areaService.stateAreas;

    this.states.subscribe(data => {
      data.forEach(v => {
        if (this.addTeam.state && v.label.toLowerCase() === this.addTeam.state.toLowerCase()) {
          this.onStateChange(v.value);
        }
      });
    });

    this.aoo.subscribe(data => {
      this.onAreaChange(this.addTeam.areaOfOperation);
    });

    this.regions.subscribe(data => {
      this.onRegionChange(this.selectedRegion);
    });

    this.userService.users.subscribe(data => {
      this.teamUsers = data.map(elem => {
        return {
          value: elem.userId,
          label: elem.getFullName()
        };
      });

      this.teamMembers = data.map(elem => {
        return {
          value: elem.userId,
          label: elem.getFullName()
        };
      });
    });
  }

  ngOnInit() {
    this.userService.getUsers().subscribe();
    this.userService.users.subscribe(users => {
      this.users.next(
        users.sort((a, b) => {
          if (a.firstName.toLowerCase() < b.firstName.toLowerCase()) {
            return -1;
          }
          if (a.firstName.toLowerCase() > b.firstName.toLowerCase()) {
            return 1;
          }
          return 0;
        })
      );

      this.userCount = users.length;
    });

    this.locationService.superCountries.subscribe(countries => {
      this.countries.next(
        countries.map(country => {
          return {
            value: country.IsoCode,
            label: country.name,
            DialCode: country.DialCode
          };
        })
      );
    });

    this.roleService.roles.subscribe(roles => {
      this.roles.next(
        roles.map(role => {
          return { value: role.userGroupId, label: role.name };
        })
      );
    });

    this.teamService.teams.subscribe(teams => {
      this.teamCount = teams.length;
      this.teams.next(teams);
      this.teamOptions.next(
        teams.map(team => {
          return { value: team.teamId, label: team.teamName };
        })
      );
    });
  }

  uploadProfileImage(event) {
    const reader = new FileReader();
    const AWS = window.AWS;
    if (event.target.files && event.target.files.length > 0) {
      const file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        AWS.config.accessKeyId = environment.corona.s3Bucket.ACCESS_KEY_ID;
        AWS.config.secretAccessKey = environment.corona.s3Bucket.SECRET_ACCESS_KEY;
        AWS.config.region = environment.corona.s3Bucket.REGION;
        const bucket = new AWS.S3({
          params: { Bucket: environment.corona.s3Bucket.NAME }
        });
        const params = { Key: file.name, Body: file };
        let imgUrl: string;
        imgUrl = "";
        const user = this.user;
        bucket.upload(params, function(error, res) {
          console.log("error", error);
          if (!error) {
            user.profilePicture = res.Location;
          }
        });
      };
    }
  }

  toggleFab() {
    this.activeFabs = !this.activeFabs;
  }

  sortBy(type) {
    this.order = type;
  }

  sortByUser(type) {
    this.userOrder = type;
  }

  getUsersForTeam(team) {
    this.router.navigate(["/home/user-management/users/" + team.teamId]);
  }

  getCountryFromTeam(teams) {
    if (teams) {
      const countries = [];
      for (const team of teams) {
        if (team && team.country && !countries.includes(team.country) && team.country !== "") {
          countries.push(team.country);
        }
      }
      return countries.join(", ");
    }
  }

  getCountryData(IsoCode) {
    const countries = this.locationService.superCountries.value.filter(v => {
      return v.IsoCode === this.user.countryIsoCode;
    });
    let countryData = [{}];

    if (countries.length > 0) {
      const tempCountryData = countries[0];
      countryData = [
        {
          Id: tempCountryData.id,
          Name: tempCountryData.name,
          IsoCode: tempCountryData.IsoCode,
          DialCode: tempCountryData.DialCode,
          IsDefault: true
        }
      ];
    }
    return countryData;
  }

  disableEmailField() {
    this.isEditNotCreate = true;
  }

  enableEmailField() {
    this.isEditNotCreate = null;
  }

  isEmpty(obj) {
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        return false;
      }
    }
    return true;
  }

  loadCountryDialCode() {
    this.locationService.superCountries.subscribe(data => {
      if (!this.isEmpty(data) && this.user.countryIsoCode !== "") {
        data.forEach(element => {
          if (element.IsoCode === this.user.countryIsoCode) {
            this.countryDialCode = element.DialCode;
          }
        });
      }
    });
  }

  getCountryDialCode($event) {
    if ($event) {
      this.user.countryIsoCode = $event;
      this.locationService.superCountries.subscribe(data => {
        if (!this.isEmpty(data) && this.user.countryIsoCode !== "") {
          data.forEach(element => {
            if (element.IsoCode === this.user.countryIsoCode) {
              this.countryDialCode = element.DialCode;
            }
          });
        }
      });
    }
  }

  // team

  viewTeamDetails(team) {
    this.teamDetails = Object.assign(new Team(), team);
    this.teamDetailsModal.show();
  }

  viewAllUsers() {
    this.usersModal.show();
  }

  editTeam(team) {
    this.addTeam = Object.assign(new Team(), team);
    this.selectedRegion = team.region;
    this.updateCountry(this.addTeam.country);
    this.teamFormModal.show();
  }

  confirmDeactivate(team) {
    this.teamForDelete = Object.assign(new Team(), team);
    this.teamAction = "deactivate";
    this.confirmDeactivateTeamModal.show();
  }

  confirmActivate(team) {
    this.teamForDelete = Object.assign(new Team(), team);
    this.teamAction = "activate";
    this.confirmDeactivateTeamModal.show();
  }

  deactivateTeam(team) {
    this.teamService.updateTeamStatus(team);
    this.confirmDeactivateTeamModal.hide();
    this.teamDetailsModal.hide();
  }

  activateTeam(team) {
    this.teamService.updateTeamStatus(team);
    this.confirmDeactivateTeamModal.hide();
    this.teamDetailsModal.hide();
  }

  createTeam(team) {
    team.areaOfOperation = this.selectedArea;
    team.region = this.selectedRegion;
    team.teamMembers.push(team.teamLead);

    const userId = this.authService.getUserId();
    team.userId = team.userId ? team.userId : userId;

    this.teamService.createOrUpdateTeam(team);
    this.teamFormModal.hide();
    this.addTeam = new Team();
    this.selectedState = "";
    this.selectedCountry = "";
    this.selectedArea = "";
    this.selectedRegion = "";
  }

  getUserNameByUserId(userId) {
    return this.userService.getUserNameByUserId(userId);
  }

  getRegionNameByRegionId(regionId) {
    const region = this.regionService.regions.value.filter(regions => {
      return regions.regionId === regionId;
    });

    if (region.length) {
      return region[0].regionName;
    }

    return "N/A";
  }

  getAreaNameByAreaId(areaId) {
    const area = this.areaService.areas.value.filter(areaValue => {
      return areaValue.areaId === areaId;
    });

    if (area.length) {
      return area[0].areaName;
    }
    return "N/A";
  }

  updateCountry(country) {
    this.country.value.forEach(v => {
      if (v.label.toLowerCase() === country.toLowerCase()) {
        this.onCountryChange(v.value);
      }
    });
  }

  onCountryChange(countryId) {
    let countryName;

    if (countryId) {
      this.selectedCountry = countryId;
      this.country.value.forEach(v => {
        if (v.value === this.selectedCountry) {
          countryName = v.label;
          this.addTeam.country = countryName;
        }
      });
      this.regionService.getCountryRegions(countryName);
    }
  }

  onRegionChange(regionId) {
    if (regionId) {
      this.selectedRegion = regionId;
      this.regionService.getRegionStates(this.selectedRegion);
      this.ref.detectChanges();
    }
  }

  onStateChange(stateId) {
    let stateName;

    if (stateId) {
      this.selectedState = stateId;
      this.states.value.forEach(v => {
        if (v.value === this.selectedState) {
          stateName = v.label;
          this.addTeam.state = stateName;
        }
      });

      this.areaService.getStateAreas(stateName);

      this.ref.detectChanges();
    }
  }

  onAreaChange(area) {
    if (area) {
      this.selectedArea = area;
      this.ref.detectChanges();
    }
  }

  onTeamLeadChange(tl) {
    if (tl) {
      this.teamMembers = this.teamUsers.filter(v => {
        return tl !== v.value;
      });
    }
  }
  closeTeamModal() {
    this.teamFormModal.hide();
    this.addTeam = new Team();
  }
}



